import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import Input from '@mui/material/Input';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { v4 as uuidv4 } from 'uuid';

const intialRows = [
  {
    value: 1500,
    date: new Date().toDateString(),
    user: 'Arisha',
    comment: 'We will win!',
    id: uuidv4(),
  }
];

export function TableOpen() {
  const [value, setValue] = React.useState('');
  const [valueError, setValueError] = React.useState(false);

  const [user, setUser] = React.useState('');
  const [userError, setUserError] = React.useState(false);

  const [comment, setComment] = React.useState('');
  const [commentError, setCommentError] = React.useState(false);

  const [rows, setRows] = React.useState(intialRows);

  const addItem = (e) => {
    e.preventDefault();

    setValueError(!value && true);
    setUserError(!user && true);
    setCommentError(!comment && true);

    if (value && user && comment) {
      setRows([...rows, {
        value,
        date: new Date().toDateString(),
        user,
        comment,
        id: uuidv4(),
      }]);

      setValue('');
      setUser('');
      setComment('');
    }

  };

  const removeItem = (id) => {
    const result = rows.filter(row => row.id !== id);

    setRows(result);
  };

  return (
    <TableContainer sx={{ maxWidth: 950 }} component={Paper}>
      <Table aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>value</TableCell>
            <TableCell align="center">date</TableCell>
            <TableCell align="center">user</TableCell>
            <TableCell align="center">comment</TableCell>
            <TableCell align="center"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(row => (
            <TableRow
              key={row.id}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.value}
              </TableCell>

              <TableCell align="center">
                {row.date}
              </TableCell>

              <TableCell align="center">
                {row.user}
              </TableCell>

              <TableCell align="center">
                {row.comment}
              </TableCell>

              <TableCell align="right">
                <Button onClick={() => removeItem(row.id)}>
                  Remove
                </Button>
              </TableCell>
            </TableRow>
          ))}
          <TableRow
            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
          >
            <TableCell component="th" scope="row">
              <Input
                type="number"
                onChange={e => setValue(e.target.value)}
                value={value}
                error={!value && valueError}
              />
            </TableCell>

            <TableCell align="right">
              {new Date().toDateString()}
            </TableCell>

            <TableCell align="right">
              <Select
                onChange={e => setUser(e.target.value)}
                value={user}
                error={!user && userError}
              >
                <MenuItem value="Valera">Valera</MenuItem>
                <MenuItem value="Misha">Misha</MenuItem>
                <MenuItem value="Dasha">Dasha</MenuItem>
              </Select>
            </TableCell>

            <TableCell align="right">
              <Input
                type="text"
                onChange={e => setComment(e.target.value)}
                value={comment}
                error={!comment && commentError}
              />
            </TableCell>

            <TableCell align="right">
              <Button onClick={addItem}>
                Add
              </Button>
              <Button onClick={window.close}>
                Close
              </Button>
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
}
