/* eslint-disable no-restricted-globals */
import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { api } from '../api';


export default function TableSG() {
  const regions = Object.entries(api);

  const popup = () => {
    const width = 950;
    const height = 200;
    const left = (screen.width / 2) - (width / 2);
    const top = (screen.height / 2) - (height / 2);

    window.open('./tablesg/about', 'popup', 'width=' + width + ',height=' + height + ',left=' + left + ',top=' + top + '');
  };

  return (
    <>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell rowSpan={2}>regions</TableCell>
              <TableCell align="center" colSpan={3}>2017</TableCell>
              <TableCell align="center" colSpan={3}>2018</TableCell>
              <TableCell align="center" colSpan={3}>2019</TableCell>
            </TableRow>
            <TableRow>
              <TableCell align="center">XX</TableCell>
              <TableCell align="center">yy</TableCell>
              <TableCell align="center">zz</TableCell>
              <TableCell align="center">xx</TableCell>
              <TableCell align="center">yy</TableCell>
              <TableCell align="center">zz</TableCell>
              <TableCell align="center">xx</TableCell>
              <TableCell align="center">yy</TableCell>
              <TableCell align="center">zz</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {regions.map((region) => (
              <TableRow
                key={region[0]}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell component="th" scope="row">
                  {region[0]}
                </TableCell>
                {Object.entries(region[1].G).map(date => {
                  return Object.entries(date[1])
                    .map(symb => (
                      <TableCell align="center" onClick={popup}>
                        {Object.values(symb[1]).map(value =>
                          <p>{value}</p>
                        )}
                      </TableCell>
                    ))
                })}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

    </>
  );
}
